import React, { useState } from "react";
import { FaCode, FaReact, FaStar, FaArrowAltCircleUp } from "react-icons/fa";
import { AiOutlineCopyrightCircle } from "react-icons/ai";
import { Link } from "react-router-dom";

const myScrollFunc = function (ele) {
  let offsetTop = document.getElementById(ele).offsetTop;
  window.scrollTo({
    top: offsetTop - 40,
    behavior: "smooth",
  });
};

const Footer = (props) => {
  const [bgStyle] = useState({
    backgroundColor: "#343a40",
    minHeight: "25vh",
    color: "white",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
  });
  const name = props.name;
  const link = props.link;

  return (
    <footer style={bgStyle} className="footer">
      <div>
        Copyright{" "}
        <AiOutlineCopyrightCircle className="react-icons"></AiOutlineCopyrightCircle>{" "}
        2020
      </div>
      <div>
        <FaCode className="react-icons"></FaCode>
        by{" "}
        <a
          className="badge badge-dark"
          rel="noopener"
          href={link}
          aria-label="My GitHub"
          style={{ color: "white", textDecoration: "none" }}
        >
          <b>{name}</b>
        </a>{" "}
        using <FaReact className="react-icons"></FaReact>{" "}
        {/* <Link
          to={""}
          onClick={() => {
            myScrollFunc("navbar");
          }}
          style={{ color: "white" }}
        >
          Scroll to Top!{" "}
        </Link>{" "}
        <FaArrowAltCircleUp className="react-icons"></FaArrowAltCircleUp> */}
      </div>
    </footer>
  );
};

export default Footer;
